.TestimonialWrp{

}
.Author{
  font-weight: 700;
  margin-bottom: 10px;
}
.TestimDate{
  font-size: .8em;
}
.TestimDate,
.Stars{
  margin-bottom: 5px;
}
.TestimonialContainer{
  padding: 5px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 5px;
  overflow: hidden;
  margin-right: -5px;
  display: inline-block;
  height: 70vh;
  line-height: 1.3;
  cursor: default;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  color: #444;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transform: translate(0, 550%) rotateX(-540deg);
          transform: translate(0, 550%) rotateX(-540deg);
}
.InnerContainer{
  border-radius: 5px;
  background: #fff;
  height: 100%;
  overflow: auto;
  padding: 14px 7px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 1px 1px 15px #333;
          box-shadow: 1px 1px 15px #333;
  border: 1px solid #eee;
  text-align: center;
}

.TestimonialContainerActive{
  -webkit-transform: translate(0, 0) rotateX(0deg);
          transform: translate(0, 0) rotateX(0deg);
}
@media (min-width:1024px){
  .InnerContainer{
    font-size: 1.15em;
  }
}
@media (max-width:768px){
  .TestimonialContainer{
    width: 100%;
    height: 90%;
    margin:0;
    background: #fff;
  }
  .TestimonialWrp{
    padding: 0 5px;
  }
}
@media (max-height:450px){
  .TestimonialContainer{
    line-height: 1;
  }
}



