.NavigationItem {
  display: inline-block;
  margin: 15px 20px;
  cursor: pointer;
  position: relative;
  z-index: 10;
}
.NavigationItem::before{
  content: "";
  display: inline-block;
  position: absolute;
  top:100%;
  left: 0;
  width: 0;
  height: 2px;
  background: #1fbdf2;
  -webkit-transition: width .5s;
  -o-transition: width .5s;
  transition: width .5s;
}
.NavigationItemActive::before{
  width: 100%;
}
.NavigationItem:last-child{
  margin-right: 40px;
}
.NavigationItem:hover::before{
  width: 100%;
}
@media (max-width:767px){
  .NavigationItem{
    display: block;
    margin-top: 25px;
    font-size: 1.5em;
  }
}



