.Logo {
  display: inline-block;
  color: #222;
  text-shadow: 1px 2px 3px #888;
  font-size: 24px;
  cursor: pointer;
  line-height: 1.6;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.Logo:hover {
  text-shadow: none;
}



